<template>
  <section class="card border-0 py-3">
    <swiper
      :slidesPerView="1.3"
      :spaceBetween="20"
      :freeMode="true"
      :modules="modules"
      :breakpoints="swiperOptions.breakpoints"
      class="mySwiper"
    >
      <template v-for="(item, index) in tempProductList" :key="index">
        <swiper-slide>
          <div>
            <img :src="item.image" class="img-fluid" alt="..." />
          </div>
        </swiper-slide>
      </template>
    </swiper>
  </section>
</template>

<style lang="scss">
@import 'swiper/css';
@import 'swiper/css/free-mode';

.swiper {
  width: 100%;
  height: 100%;
}

.swiper-slide {
  text-align: center;
  font-size: 18px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.swiper-slide img {
  display: block;
  width: 100%;
  height: 100%;
  object-fit: cover;
}
</style>

<script>
import { Swiper, SwiperSlide } from 'swiper/vue';
import { FreeMode } from 'swiper/modules';
import { apiGetProducts } from '@/assets/javascript/api';

export default {
  data() {
    return {
      swiperOptions: {
        breakpoints: {
          767: {
            slidesPerView: 3.2,
            spaceBetween: 20,
          },
          1024: {
            slidesPerView: 4.1,
            spaceBetween: 30,
          },
        },
      },
      tempProductList: [],
      productList: [],
    };
  },
  props: ['choseType'],
  watch: {
    choseType() {
      this.selectTempProducts(this.choseType);
    },
  },
  components: {
    Swiper,
    SwiperSlide,
  },
  methods: {
    selectTempProducts(select) {
      this.tempProductList = [];
      this.productList.forEach((item) => {
        if (item.category === select) {
          this.tempProductList.push(item);
        }
      });
    },
    getProducts() {
      apiGetProducts()
        .then((res) => {
          this.productList = res.data.data;
          this.selectTempProducts('coffee');
        })
        .catch((err) => {
          console.log(err);
        });
    },
  },
  setup() {
    return {
      modules: [FreeMode],
    };
  },
  mounted() {
    this.getProducts();
  },
};
</script>
